<template>
    <div class="banner">
        <div class="banner-top">
            <div class="top-left" @click="toCity">
                {{cityname}}
                <i class="iconfont">&#xe6aa;</i>
            </div>
            <div class="top-search">
                <i class="iconfont">&#xe62c;</i>
                泰国
            </div>
            <div class="top-right" @click="Onshow()">
                <i class="iconfont">&#xe850;</i>
            </div>
        </div>
        <div class="banner-swiper">
            <van-swipe class="my-swipe" :autoplay="3000" indicator-color="orange">
               <van-swipe-item v-for="(item,index) in list" :key="index"><img :src="item.imgUrl" alt=""></van-swipe-item>
             </van-swipe>
        </div>
        <transition 
           enter-active-class=" animate__fadeInLeft"
           leave-active-class=" animate__fadeOutLeft"
        >
             <div class="toshow animate__animated" v-show="toshow">
                 黑色
             </div>
        </transition>
    </div>
</template>


<script>
import {mapState} from 'vuex'
import 'animate.css'
export default {
    data() {
        return {
           list:[
               {
                   id:0,
                   imgUrl:require('@/assets/images/5a93ecdba426b.jpg')
               },
               {
                   id:1,
                   imgUrl:require('@/assets/images/5c8b3e07ee325.jpg')
               },
               {
                   id:2,
                   imgUrl:require('@/assets/images/5cb0405d0a9f9.jpg')
               },
               {
                   id:3,
                   imgUrl:require('@/assets/images/5e707c4753842.jpg')
               },
               {
                   id:4,
                   imgUrl:require('@/assets/images/5f0ad629daa60.jpg')
               },
               {
                   id:5,
                   imgUrl:require('@/assets/images/5f0c154798d9a.jpg')
               },
               {
                   id:6,
                   imgUrl:require('@/assets/images/5f02c28e98d91.jpg')
               },
           ],
           toshow:false
        }
    },
    computed: {
        ...mapState(['cityname'])
    },
    methods: {
        Onshow(){
           this.toshow = !this.toshow;
        },
        toCity(){
            this.$router.push({name:'City',params:{id:this.$store.state.cityname}})
        }
    },
}
</script>

<style scoped>
    .banner{
        width: 100%;
        height: 3.6rem;
        border: .013333rem solid #000;
        position: relative;
    }
    .banner>.banner-top{
        width: 100%;
        height: .666667rem;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .toshow{
        position: fixed;
        top: 0;
        left: 0;
        z-index: 100;
        width: 3.333333rem;
        height: 100%;
        background:rgba(0, 0, 0, .7);
        color: #fff;
    }
    .top-left{
        width: 1.333333rem;
        height: .666667rem;
        line-height: .666667rem;
        text-align: center;
        font-size: .266667rem;
        color: #fff;
    }
    .top-search{
        flex: 1;
        background-color: #fff;
        border-radius: .133333rem;
        height: .466667rem;
        line-height: .466667rem;
        padding-left: .133333rem;
        box-sizing: border-box;
        font-size: .293333rem;
        color: #666;
    }
    .top-search i{
        font-size: .4rem;
    }
    .top-right{
        width: .8rem;
        height: .666667rem;
        line-height: .666667rem;
        text-align: center;
        color: #fff;
    }
    .top-right i{
        font-size: .4rem;
    }
    .banner-swiper{
        width: 100%;
        height: 3.6rem;
    }
    .my-swipe{
        width: 100%;
        height: 3.6rem;
    }
    .banner-swiper img{
        width: 100%;
        height: 100%;
    }
</style>