<template>
  <div class="splick">
    <van-swipe :autoplay="4000" indicator-color="orange">
      <van-swipe-item v-for="(image, index) in list" :key="index">
          <div class="top">
            <img :src="image.img" alt="">
            <div class="reser"><p>距离结束</p><van-count-down :time="time" format="DD 天 HH 时 mm 分 ss 秒" /></div>
          </div>
          <div class="content" @click="Item(image.liimg,image.title,image.price)">
            <div class="img">
              <img :src="image.liimg" alt="">
            </div>
            <div class="msg">
              <div class="title">{{image.title}}</div>
              <div class="price">￥{{image.price}}</div>
            </div>
          </div>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list:[
        {
          img:'https://picbed.qunarzz.com/50214952fbf248f06af43a222c82a532.png',
          liimg:'https://img1.qunarzz.com/sight/p0/2008/de/de0775c07108df3a3.water.jpg_256x160_83a8a0c4.jpg',
          title:'长隆水上乐园全球必去的水上乐园',
          price:'95.8'
        },
        {
          img:'https://picbed.qunarzz.com/50214952fbf248f06af43a222c82a532.png',
          liimg:'https://img1.qunarzz.com/sight/p0/2008/6d/6da8a20b21da1340a3.water.jpg_256x160_eb7ed443.jpg',
          title:'长隆野生动物世界奇妙的朋友与您共享欢乐',
          price:'207'
        },
        {
          img:'https://picbed.qunarzz.com/50214952fbf248f06af43a222c82a532.png',
          liimg:'https://img1.qunarzz.com/sight/p0/1807/f3/f3a998c29724c22aa3.water.jpg_256x160_23f3118d.jpg',
          title:'广州长隆旅游度假区一站式旅游度假圣地',
          price:'75'
        },
        {
          img:'https://picbed.qunarzz.com/50214952fbf248f06af43a222c82a532.png',
          liimg:'https://img1.qunarzz.com/sight/p0/2007/fb/fb72d26990ce1366a3.water.jpg_256x160_d855bfb7.jpg',
          title:'不负盛夏好时光,你若安好便是晴天',
          price:'90'
        },
      ],
      time:30 * 60 * 60 * 1000,
      arr:[]
    };
  },
  created() {
    this.$axios.get('/api/ajax/movieOnInfoList?token=&optimus_uuid=84FEAC60E81911E9A13F839F49C95F45275769ED0E6442EF9DEEA13EE7F23CAB&optimus_risk_level=71&optimus_code=10').then((res) =>{
          console.log(res);
    })
  },
  methods: {
    Item(i,t,p){
      let obj ={};
      obj.img =i;
      obj.title =t;
      obj.price =p;
      this.arr.push(obj);
      let isfalse = false;
      if(JSON.parse(localStorage.getItem('Item'))){
        let cart = JSON.parse(localStorage.getItem('Item'));
        cart.forEach(item =>{
          if(obj.title == item.title){
              isfalse = true;
          }
        })
        if(!isfalse){
          let item = JSON.parse(localStorage.getItem('Item'));
          item.push(obj);
          localStorage.setItem('Item',JSON.stringify(item));
        }
      }else{
        localStorage.setItem('Item',JSON.stringify(this.arr));
      }
    },
  },
};
</script>

<style scoped>
.van-swipe-item{
  padding: 0.133333rem 0.266667rem;
  box-sizing: border-box;
}
  .top{
    width: 100%;
    height: 1.333333rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .top img{
    width: 2.666667rem;
    height: 0.933333rem;
  }
  .top>.reser{
    flex: 1;
    margin-left: 0.133333rem;
    font-size: 0.266667rem;
  }
  .reser{
    display: flex;
    align-items: center;
  }
  .reser p{
    width: 2rem;
    font-weight: bold;
    font-size: 0.4rem;
  }
  .reser>.van-count-down{
    flex: 1;
  }
  .content{
    display: flex;
    width: 100%;
    height: 3.333333rem;
  }
  .content>.img{
    width: 3.333333rem;
    height: 2rem;
  }
  .content>.img img{
    width: 100%;
    height: 100%;
  }
  .content>.msg{
    margin-left: 0.133333rem;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 0.4rem;
    height: 2rem;
  }
  .content>.msg>.price{
    color: orangered;
  }
</style>
