<template>
  <div class="like">
    <div class="like-top">特惠旅游</div>
    <div class="router">
      <div
        @click="changecurIndex(index)"
        :class="curIndex == index ? 'active' : ''"
        v-for="(item, index) in routerItem"
        :key="index"
      >
        {{ item }}
      </div>
    </div>
    <div class="box">
      <div
        class="content"
        v-for="(item, index) in contentItem[curIndex]"
        :key="index"
        @click="setItem(item.img,item.menu,item.price)"
      >
        <div class="img">
          <img :src="item.img" alt="" />
        </div>
        <div class="menu">{{ item.menu }}</div>
        <div class="desc">{{ item.desc }}</div>
        <div class="price">￥{{ item.price }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      routerItem: ["当季热门度假", "今日特惠度假"],
      contentItem: [
        [
          {
            img:
              "https://imgs.qunarzz.com/p/p48/201302/28/bc44faa497db0dcf93835fbb.jpg_256x160_0ba13461.jpg",
            title: "北京-泰国",
            desc: "7天跟团游",
            menu: "含6晚住宿",
            price: 4524,
          },
          {
            img:
              "https://imgs.qunarzz.com/sight/p0/1806/d0/d033ab35c038b7c4a3.img.jpg_256x160_b4b0587c.jpg",
            title: "北京-青海湖",
            desc: "6天跟团游",
            menu: "含往返机票(含税)+5晚住宿",
            price: 2228,
          },
          {
            img:
              "https://imgs.qunarzz.com/p/p66/201304/17/384810d022dd28f793835fbb.jpg_256x160_6a7ec251.jpg",
            title: "北京-三亚",
            desc: "5天跟团游",
            menu: "含往返机票(含税)+4晚住宿",
            price: 3080,
          },
          {
            img:
              "https://imgs.qunarzz.com/p/p29/201302/28/3d20251a1b60350a93835fbb.jpg_256x160_e8f468b0.jpg",
            title: "北京-日本",
            desc: "8天跟团游",
            menu: "含往返机票(含税)+7晚住宿",
            price: 8999,
          },
        ],
        [
          {
            img:
              "https://img1.qunarzz.com/sight/p0/1409/19/adca619faaab0898245dc4ec482b5722.jpg_256x160_f977b5d4.jpg",
            menu: "故宫",
            desc: "世界五大宫之首,穿越与您近在咫尺",
            price: 64.9,
          },
          {
            img:
              "https://img1.qunarzz.com/sight/p0/2005/39/3979f1867defec4ea3.water.jpg_256x160_6ae804bb.jpg",
            menu: "八达岭长城",
            desc: "一定要看那块“不到长城非好汉”碑",
            price: 45,
          },
          {
            img:
              "https://img1.qunarzz.com/sight/p0/2005/30/307fa37039eab5cba3.water.jpg_256x160_643266e5.jpg",
            menu: "颐和园",
            desc: "北方也有江南园林",
            price: 30,
          },
          {
            img:
              "https://img1.qunarzz.com/sight/p0/1505/f5/f5f45e1a83537bcb.water.jpg_256x160_618d3a8e.jpg",
            menu: "圆明园",
            desc: "追忆昔日万园之园",
            price: 10,
          },
        ],
      ],
      curIndex: 0,
      obj: [],
      arr:[]
    };
  },
  methods: {
    changecurIndex(index) {
      this.curIndex = index;
    },
    setItem(i,m,p){
      let obj ={};
      obj.img =i;
      obj.title =m;
      obj.price =p;
      this.arr.push(obj);
      let isfalse = false;
      if(JSON.parse(localStorage.getItem('Item'))){
        let cart = JSON.parse(localStorage.getItem('Item'));
        cart.forEach(item =>{
          if(obj.title == item.title){
              isfalse = true;
          }
        })
        if(!isfalse){
          let item = JSON.parse(localStorage.getItem('Item'));
          item.push(obj);
          localStorage.setItem('Item',JSON.stringify(item));
        }
      }else{
        localStorage.setItem('Item',JSON.stringify(this.arr));
      }
    },
  },
};
</script>

<style scoped>
.like {
  margin: 0.133333rem 0.266667rem 1.333333rem 0.266667rem;
}
.like-top {
  font-size: 0.6rem;
  font-weight: bold;
}
.router {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.router div {
  margin: 0.133333rem;
  width: 3.333333rem;
  border: 1px solid #ddd;
  text-align: center;
  line-height: 0.8rem;
  height: 0.8rem;
  font-size: 0.4rem;
}
.box {
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin-bottom: 1.333333rem;
}
.content {
  width: 47%;
  float: left;
  margin: 0.133333rem;
}
.content > .img {
  width: 100%;
  height: 2.666667rem;
}
.content > .img img {
  width: 100%;
  height: 100%;
}
.content > .menu {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  height: 0.666667rem;
  font-size: 0.4rem;
  margin: 0.133333rem;
}
.content > .desc {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  height: 0.666667rem;
  font-size: 0.266667rem;
  color: #ddd;
  margin: 0.133333rem;
}
.content > .price {
  margin: 0.133333rem;
  color: orangered;
}
.active {
  background-color: skyblue;
  color: #fff;
  border: none;
}
</style>
